





















































































































































#QualityControl {
  padding: 15px;
  width: 100%;
  height: 100%;
  .controlBox {
    width: 100%;
    height: 100%;
    overflow: hidden;
    .left {
      float: left;
      width: 49%;
      height: 100%;
    }
    .right {
      position: relative;
      float: right;
      width: 49%;
      height: 100%;
    }
  }
  .exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    margin-right: 10px;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
  .queryBtns {
    text-align: right;
    margin-bottom: 10px;
  }
}
